"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { houseSaleData, getHouseByYear, getYearRange, HouseSalePoint } from "./data";
import TimelineSlider from "@/components/TimeMachine/TimelineSlider";
import HouseDetailCard from "@/components/TimeMachine/HouseDetailCard";
import AIAnalysis from "@/components/TimeMachine/AIAnalysis";

export default function TimeMachinePage() {
  const [currentYear, setCurrentYear] = useState(2000);
  const [currentHouse, setCurrentHouse] = useState<HouseSalePoint | null>(null);
  const [showAIAnalysis, setShowAIAnalysis] = useState(false);

  const yearRange = getYearRange();
  const availableYears = houseSaleData.map(house => house.year).sort((a, b) => a - b);

  // Update current house when year changes
  useEffect(() => {
    const house = getHouseByYear(currentYear);
    setCurrentHouse(house || null);
  }, [currentYear]);

  // Initialize with first available year
  useEffect(() => {
    if (availableYears.length > 0) {
      setCurrentYear(availableYears[0]);
    }
  }, []);

  const handleYearChange = (year: number) => {
    setCurrentYear(year);
  };

  const getMarketContext = () => {
    if (!currentHouse) return null;

    const year = currentHouse.year;
    let context = "";
    let contextColor = "text-gray-600";

    if (year >= 2000 && year <= 2006) {
      context = "Housing Boom Era - Rapid price appreciation and easy credit";
      contextColor = "text-green-600";
    } else if (year >= 2007 && year <= 2009) {
      context = "Financial Crisis - Market peak and beginning of downturn";
      contextColor = "text-red-600";
    } else if (year >= 2010 && year <= 2012) {
      context = "Post-Crisis Recovery - Market bottom and stabilization";
      contextColor = "text-orange-600";
    } else if (year >= 2013 && year <= 2019) {
      context = "Steady Recovery - Gradual price increases and market normalization";
      contextColor = "text-blue-600";
    } else if (year >= 2020) {
      context = "Pandemic Era - Remote work driving demand and price surges";
      contextColor = "text-purple-600";
    }

    return { context, contextColor };
  };

  const marketContext = getMarketContext();

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <Link 
                href="/"
                className="text-gray-600 hover:text-gray-900 transition-colors duration-200"
              >
                ← Back to Home
              </Link>
              <div className="h-6 w-px bg-gray-300"></div>
              <h1 className="text-2xl font-bold text-gray-900">House Time Machine</h1>
            </div>
            
            <button
              onClick={() => setShowAIAnalysis(!showAIAnalysis)}
              className="bg-black text-white px-4 py-2 rounded-lg font-medium hover:bg-gray-800 transition-colors duration-200"
            >
              {showAIAnalysis ? "Hide AI Analysis" : "Show AI Analysis"}
            </button>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="space-y-8">
          {/* Market Context Banner */}
          {marketContext && (
            <div className="bg-white rounded-lg p-4 shadow-sm border-l-4 border-black">
              <div className="flex items-center">
                <div className="mr-3 text-2xl">📊</div>
                <div>
                  <h3 className="font-semibold text-gray-900">Market Context</h3>
                  <p className={`text-sm ${marketContext.contextColor}`}>
                    {marketContext.context}
                  </p>
                </div>
              </div>
            </div>
          )}

          {/* Timeline Slider */}
          <div className="bg-white rounded-2xl shadow-lg p-8">
            <TimelineSlider
              min={yearRange.min}
              max={yearRange.max}
              value={currentYear}
              onChange={handleYearChange}
              availableYears={availableYears}
            />
          </div>

          {/* House Detail Card */}
          <HouseDetailCard house={currentHouse} />

          {/* Statistics Overview */}
          {currentHouse && (
            <div className="bg-white rounded-2xl shadow-lg p-8">
              <h2 className="text-xl font-bold text-gray-900 mb-6">Market Statistics</h2>
              <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {availableYears.indexOf(currentYear) + 1}
                  </div>
                  <div className="text-sm text-gray-600">of {availableYears.length} records</div>
                </div>
                
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {currentYear < 2010 ? "📈" : currentYear < 2015 ? "📉" : "📈"}
                  </div>
                  <div className="text-sm text-gray-600">Market Trend</div>
                </div>
                
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {Math.round(currentHouse.price / 1000)}K
                  </div>
                  <div className="text-sm text-gray-600">Price (thousands)</div>
                </div>
                
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {Math.round(currentHouse.price / currentHouse.sqft)}
                  </div>
                  <div className="text-sm text-gray-600">$ per sq ft</div>
                </div>
              </div>
            </div>
          )}

          {/* AI Analysis Section */}
          {showAIAnalysis && (
            <AIAnalysis currentHouseData={currentHouse} />
          )}

          {/* Data Source Notice */}
          <div className="bg-gray-100 rounded-lg p-6 text-center">
            <p className="text-sm text-gray-600">
              <strong>Note:</strong> This is a demonstration using sample data. 
              Real estate prices and trends are simplified for educational purposes.
            </p>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-white border-t mt-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center text-gray-600">
            <p className="text-sm">
              House Time Machine - Explore real estate history through interactive data visualization
            </p>
          </div>
        </div>
      </footer>
    </div>
  );
}
